'use client'

import { getAnimeDetail } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Play, Heart, Share2 } from 'lucide-react'
import { notFound } from 'next/navigation'

interface AnimePageProps {
  params: Promise<{ id: string }>
}

export default async function AnimePage({ params }: AnimePageProps) {
  // ⚡ unwrap params Promise
  const { id } = await params
  const animeId = parseInt(id)
  if (isNaN(animeId)) notFound()

  let anime = null
  try {
    anime = await getAnimeDetail(animeId)
  } catch (err) {
    console.error('Error fetching anime:', err)
    notFound()
  }

  if (!anime) notFound()

  const imageUrl = anime.images?.jpg?.large_image_url || '/placeholder.svg'
  const titleEnglish = anime.title_english || '-'
  const score = anime.score != null ? anime.score.toFixed(1) : '-'
  const episodes = anime.episodes ?? 'TBA'
  const status = anime.status || '-'
  const year = anime.year ?? 'TBA'
  const genres = anime.genres ?? []
  const studios = anime.studios ?? []
  const synopsis = anime.synopsis || '-'
  const airedFrom = anime.aired?.from ? new Date(anime.aired.from).toLocaleDateString() : 'TBA'
  const airedTo = anime.aired?.to ? new Date(anime.aired.to).toLocaleDateString() : 'Ongoing'

  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      {/* Hero Banner */}
      <div className="relative h-96 w-full overflow-hidden">
        <Image
          src={imageUrl}
          alt={anime.title || 'Anime'}
          fill
          className="object-cover"
          loading="eager" // ✅ LCP fix
          priority
        />
        <div className="absolute inset-0 bg-black/30 md:hover:bg-black/50 transition-colors" />
      </div>

      <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 -mt-20 relative z-10 mb-12">
          {/* Poster */}
          <div className="md:col-span-1 flex justify-center">
            <div className="relative w-full max-w-xs rounded-lg overflow-hidden shadow-lg hover:shadow-2xl transition-shadow duration-300">
              <Image
                src={imageUrl}
                alt={anime.title || 'Anime'}
                width={300}
                height={400}
                className="w-full h-auto"
                loading="eager"
              />
              {/* Play Button */}
              <div className="absolute inset-0 flex items-center justify-center opacity-0 hover:opacity-100 transition-opacity">
                <div className="w-14 h-14 bg-primary/80 rounded-full flex items-center justify-center">
                  <Play size={24} className="text-white" />
                </div>
              </div>
              {/* Favorite Button */}
              <button className="absolute top-2 left-2 p-2 bg-white/90 rounded-full border border-gray-300 hover:bg-red-500 hover:text-white transition-colors">
                <Heart size={18} className="text-gray-700 hover:text-white" />
              </button>
            </div>
          </div>

          {/* Details */}
          <div className="md:col-span-2 pt-6">
            <h1 className="text-4xl font-bold text-foreground mb-2">{anime.title}</h1>
            <p className="text-muted-foreground mb-6">{titleEnglish}</p>

            {/* Stats */}
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
              {[
                { label: 'Rating', value: score },
                { label: 'Episodes', value: episodes },
                { label: 'Status', value: status },
                { label: 'Year', value: year },
              ].map((stat) => (
                <div key={stat.label} className="bg-card p-4 rounded-lg border border-border text-center">
                  <p className="text-muted-foreground text-sm">{stat.label}</p>
                  <p className="text-2xl font-bold text-primary">{stat.value}</p>
                </div>
              ))}
            </div>

            {/* Action Buttons */}
            <div className="flex flex-wrap gap-3 mb-8">
              <Button className="bg-primary hover:bg-primary/90 text-white gap-2">
                <Play size={18} /> Watch Now
              </Button>
              <Button variant="outline" className="border-primary text-primary hover:bg-primary/10 gap-2">
                <Heart size={18} /> Add to Favorites
              </Button>
              <Button variant="outline" className="border-border gap-2">
                <Share2 size={18} /> Share
              </Button>
            </div>

            {/* Genres */}
            {genres.length > 0 && (
              <div className="mb-8">
                <h3 className="text-sm font-semibold text-muted-foreground mb-2">GENRES</h3>
                <div className="flex flex-wrap gap-2">
                  {genres.map((genre) => (
                    <span
                      key={genre.mal_id}
                      className="px-3 py-1 bg-card border border-primary text-primary text-sm rounded-full hover:shadow-md transition-shadow duration-300 cursor-pointer"
                    >
                      {genre.name}
                    </span>
                  ))}
                </div>
              </div>
            )}

            {/* Studios */}
            {studios.length > 0 && (
              <div className="mb-8">
                <h3 className="text-sm font-semibold text-muted-foreground mb-2">STUDIOS</h3>
                <div className="flex flex-wrap gap-2">
                  {studios.map((studio) => (
                    <span key={studio.mal_id} className="text-foreground">
                      {studio.name}
                    </span>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Synopsis */}
        {synopsis && (
          <section className="mb-12">
            <h2 className="text-2xl font-bold text-foreground mb-4">Synopsis</h2>
            <p className="text-foreground/80 leading-relaxed">{synopsis}</p>
          </section>
        )}

        {/* Aired Info */}
        <section className="mb-12">
          <h2 className="text-2xl font-bold text-foreground mb-4">Air Dates</h2>
          <p className="text-foreground/80">{airedFrom} - {airedTo}</p>
        </section>
      </div>
    </main>
  )
}
